<template>
  <div>
    <h1>keyup+click</h1>
    <p></p>
    <div class="d-flex align-center">
      <v-text-field
        v-model="value"
        label=""
        dense
        outlined
        hide-details
        style="max-width: 200px;"
        class="mr-2"
        @keyup.enter="handleSubmit"
      />
      <v-btn
        class="mr-2"
        @click="handleSubmit"
      >
        
      </v-btn>
      <div>: {{ status }}</div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      value: '',
      status: ''
    }
  },
  methods: {
    handleSubmit() {
      this.status = '' + this.value
      setTimeout(() => {
        this.status = ''
        setTimeout(() => {
          this.reset()
        }, 1000)
      }, 1000)
    },
    reset() {
      this.value = ''
      this.status = ''
    }
  }
}
</script>

