<template>
  <div>
    Demo1 checkbox  
    <br>
    2
    <div>
      <v-btn @click="handleSelectAll">
        /
      </v-btn>
      <hr>
      <v-checkbox
        v-for="authObj in authList"
        :key="authObj.id"
        v-model="selected"
        :value="authObj"
        class="mr-4"
        style="display: inline-flex;width: calc(22%);"
        @click="() => {}"
      >
        <template #label>
          {{ authObj.name }}
        </template>
      </v-checkbox>
      <hr>
      : {{ selected.length }}
      <hr>
      selected: {{ selected }}
    </div>
  </div>
</template>

<script>

export default {
  data() {
    return {
      selected: [
      ],
      authList: [
        {
          id: 1,
          name: '1',
          code: '1'
        },
        {
          id: 2,
          name: '2',
          code: '2'
        },
        {
          id: 3,
          name: '3',
          code: '3'
        },
      ]
    };
  },
  mounted() {
    // ! BUG:  
    // 10 { id: index, name: '' + index, code: index }
    this.selected = Array.from({ length: 10 }, (v, i) => ({ id: i + 1, name: `${i + 1}`, code: i + 1 }));
  },
  methods: {
    handleSelectAll() {
      const selList = [...this.selected];
      // 
      if (this.authList.every(item => !this.selected.includes(item))) {
        this.selected = selList.concat(this.authList);
        console.log('', this.selected);
      } else {
        // 
        const idlist = this.authList.map(item => item.id);
        this.selected = selList.filter(item => !idlist.includes(item.id));
        console.log('', this.selected);
      }
    },
  },
};
</script>

